<template>
     <div class="cinema_body">
        <Loading v-if="isLoading" />
        <scroller v-else>
        <ul>
            <!-- <li>
                <div>
                    <span>大地影院(澳东世纪店)</span>
                    <span class="q"><span class="price">22.9</span> 元起</span>
                </div>
                <div class="address">
                    <span>金州区大连经济技术开发区澳东世纪3层</span>
                    <span>1763.5km</span>
                </div>
                <div class="card">
                    <div>小吃</div>
                    <div>折扣卡</div>
                </div>
            </li> -->
            <li v-for="item in cinemaList" :key="item.id">
                <div>
                    <span>{{ item.nm }}</span>
                    <span class="q"><span class="price">{{ item.sellPrice }}</span> 元起</span>
                </div>
                <div class="address">
                    <span>{{ item.addr }}</span>
                    <span>{{ item.distance }}</span>
                </div>
                <div class="card">
                    <div v-for="(num,key) in item.tag" v-show="num===1" :key="key" :class=" key | classCard ">{{ key | formatCard }}</div>
                    <!-- 这里的key就是对象内部的键值 -->
                </div>
            </li>
        </ul>
        </scroller>
    </div>
</template>
<script>
export default {
    name: "clist",
    data: function(){
        return {
            cinemaList: [],
            isLoading: true,
            preID: -1,
        }
    },
    methods:{},
    activated: function(){
        var _this = this;
        var cityID = this.$store.state.city.id;
        if(this.preID === cityID){return;}
        this.isLoading = true;
        this.$http.get("http://39.97.33.178/api/cinemaList?cityId=" + cityID).then(function(res){
            if(res.data.status === 0){
                // console.log(res.data);
                _this.cinemaList = res.data.data.cinemas;
                _this.isLoading = false;
                _this.preID = cityID;
            }
            else{
                console.error("获取失败");
                
            }
        })
    },
    filters:{
        formatCard: function(key){        
            var card = [
                { key : 'allowRefund' , value : '改签' },
                { key : 'endorse' , value : '退' },
                { key : 'sell' , value : '折扣卡' },
                { key : 'snack' , value : '小吃'}
            ];
            for(var i=0;i<card.length;i++){
                if(card[i].key === key){
                    return card[i].value
                }
            }
                return "";
        },
        classCard: function(key){
            var card = [
                { key : 'allowRefund' , value : 'bl' },
                { key : 'endorse' , value : 'bl' },
                { key : 'sell' , value : 'ol' },
                { key : 'snack' , value : 'ol'}
            ];
            for(var i=0;i<card.length;i++){
                if(card[i].key === key){
                    return card[i].value
                }
            }
            return "";
        },
    }
}
</script>
<style lang="scss" scoped>
#content .cinema_body{ flex:1; overflow:auto;}
.cinema_body ul{ padding:20px;}
.cinema_body li{  border-bottom:1px solid #e6e6e6; margin-bottom: 20px;}
.cinema_body div{ margin-bottom: 10px;}
.cinema_body .q{ font-size: 11px; color:#f03d37;}
.cinema_body .price{ font-size: 18px;}
.cinema_body .address{ font-size: 13px; color:#666;}
.cinema_body .address span:nth-of-type(2){ float:right; }
.cinema_body .card{ display: flex;}
.cinema_body .card div{ padding: 0 3px; height: 15px; line-height: 15px; border-radius: 2px; color: #f90; border: 1px solid #f90; font-size: 13px; margin-right: 5px;}
.cinema_body .card div.or{ color: #f90; border: 1px solid #f90;}
.cinema_body .card div.bl{ color: #589daf; border: 1px solid #589daf;}
</style>
